<script>
// import starmerx from '@/utils/starmerx'
import imgEditor from './components/imgEditor.vue'
// import { amazon_store_list } from '@/api/amazon'

// import * as http from '@/api/picture_tool'
import PopImg from './components/PopImg.vue'
const doResize = (el, binding, vnode) => {
  const { value } = binding
  const bottomOffset = (value && value.bottomOffset) || 10

  let height = window.innerHeight - el.getBoundingClientRect().top - bottomOffset
  el.style.height = height + 'px'
  window.onload = () => {
    height = window.innerHeight - el.getBoundingClientRect().top - bottomOffset
    el.style.height = height + 'px'
  }
}
export default {
  components: { PopImg, imgEditor },
  directives: {
    'sm-table-height-adaptive': {
      bind(el, binding, vnode) {
        el.resizeListener = () => {
          doResize(el, binding, vnode)
        }
        if (window) {
          window.addEventListener('resize', el.resizeListener)
        }
      },
      inserted(el, binding, vnode) {
        doResize(el, binding, vnode)
      },
      unbind(el) {
        if (window) {
          window.removeEventListener('resize', el.resizeListener)
        }
      }
    }
  },
  data() {
    return {
      searchFrontValue: '',
      frontSelected: [],
      zhutiSelect: '',
      sucaiSelected: '',
      AmazonStoreOptions: [],
