      frontLoading: false,
      frontPicture_pageTotal: 0,
      texturePictureData: [],
      purePictureData: [],
      fontForm: {
        fontFamily: 'Georgia, serif',
        backgroundColor: 'rgba(255, 255, 255, 1)',
        fontSize: 48,
        fill: 'rgba(0, 0, 0, 1)',
        fontWeight: 'normal',
        fontStyle: 'normal',
        underline: false,
        textAlign: 'left',
        // strokeWidth: 0,
        // stroke: 'rgba(0, 0, 0, 1)'
        padding: 0,
      },
      fontValue: '',
      // 浏览器安全字体
      browserSecurityFonts: [
        'Georgia, serif',
        '"Palatino Linotype", "Book Antiqua", Palatino, serif',
        '"Times New Roman", Times, serif',
        'Arial, Helvetica, sans-serif',
        'Arial Black, Gadget, sans-serif',
        '"Comic Sans MS", cursive, sans-serif',
        'Impact, Charcoal, sans-serif',
        '"Lucida Sans Unicode", "Lucida Grande", sans-serif',
        'Tahoma, Geneva, sans-serif',
        '"Trebuchet MS", Helvetica, sans-serif',
        'Verdana, Geneva, sans-serif',
        '"Courier New", Courier, monospace',
        '"Lucida Console", Monaco, monospace'
      ],
      // 渐变相关
      gradientForm: {
        gType: 'linear',
        colorStops: [
          { offset: 0, color: 'rgba(255, 0, 0, 1)' },
          { offset: 1, color: 'rgba(0, 255, 247, 1)'}
        ]
      },
      uploadFileList: [],
      pageLoading: false,
    }
  },
  watch: {
    selectType(val) {
      if (val === '图库') {
        this.searchPicture()
      } else if (val === '前景') {
        this.searchFrontPicture()
      } else if (val === '纯色') {
        this.getPureAndTexturePicture()
      }
    },
    spuValue(val) {
      const spuNumber = {
        PBY_0WP1: 5,
        PBY_0XOQ: 4,
        PEL_0MH5: 3,
        PHO_0QZA: 3,
        PHO_0S1T: 3,
        PHO_0S7N: 5,
        PHO_0VGG: 2,
        PHO_0VGL: 3,
        PHO_0W5A: 5,
        PHO_0WIT: 2,
        PTO_0XLB: 2,
        PTO_0XLV: 5,
        PTO_0XR1: 3
      }
      this.finishedPictureData = []
      for (let i = 1; i <= spuNumber[val]; i++) {
        this.finishedPictureData.push(`static/tmp_img/${val}/${i}.png`)
      }
    }
  },
  created() {
    this.getAllSPU()
    this.getAmazonStoreOptions()
    this.searchPicture()
  },
