      //     let addPictureArr = []
      //     res.data.data.hits.forEach(item => {
      //       item.number = Math.floor(Math.random() * (99 - 1) + 1)
      //     })
      //     addPictureArr = res.data.data.hits
      //     addPictureArr.map(v => {
      //       this.pictureData.push(v)
      //     })
      //     this.$forceUpdate()
      //   }
      // })
    },
