    getAllSPU() {
      // http.getAllSPU().then(res => {
      //   const showSpu = [
      //     'PBY_0WP1',
      //     'PBY_0XOQ',
      //     'PEL_0MH5',
      //     'PHO_0QZA',
      //     'PHO_0S1T',
      //     'PHO_0S7N',
      //     'PHO_0VGG',
      //     'PHO_0VGL',
      //     'PHO_0W5A',
      //     'PHO_0WIT',
      //     'PTO_0XLB',
      //     'PTO_0XLV',
      //     'PTO_0XR1'
      //   ]
      //   if (!res.data.code) {
      //     this.spuOptions = res.data.data.filter(item => showSpu.includes(item))
      //   }
      // })
    },
