      this.frontPictureData = []
      this.moreFrontLoading = false
      const parmas = {
        q: this.searchFrontValue,
        per_page: 30,
        page: 1
      }
      this.frontLoading = true
      // http.searchFrontPicture(parmas).then(res => {
      //   if (!res.data.code) {
      //     this.frontLoading = false
      //     this.frontPictureData = res.data.data
      //     // this.frontPicture_pageTotal = res.data.data.total
      //   }
      // })
    },
    getPureAndTexturePicture() {
      this.moreFrontLoading = false
      this.frontLoading = true
      // http.getTexturePicture().then(res => {
      //   if (!res.data.code) {
      //     this.frontLoading = false
      //     this.texturePictureData = res.data.data
      //   }
      // }).finally(() => {
      //   this.moreFrontLoading = false
      //   this.frontLoading = false
      // })
      // http.getPurePicture().then(res => {
      //   if (!res.data.code) {
      //     this.frontLoading = false
      //     this.purePictureData = res.data.data
      //   }
      // }).finally(() => {
      //   this.moreFrontLoading = false
      //   this.frontLoading = false
      // })
    },

    getSPUInfo() {
      const params = {
        spu: this.spuValue
      }
      this.spuInfo = []
      // http.getSPUInfo(params).then(res => {
      //   if (!res.data.code) {
      //     this.spuInfo = res.data.data
      //     this.selectedList = []
      //   }
      // })
    },
    frontPictureClick(item) {
      if (this.frontSelected.includes(item)) {
        this.frontSelected = this.frontSelected.filter(key => key !== item)
      } else {
        this.frontSelected.push(item)
      }
      if (this.frontImgUrl === item) {
        this.$refs.imgEditor.addFrontImg()
      } else {
        this.frontImgUrl = item
      }
    },
    pictureClick(item) {
      this.sucaiSelected = item
      if (this.backImgUrl === item.largeImageURL) {
        this.$refs.imgEditor.addBackImg()
      } else {
        this.backImgUrl = item.largeImageURL
      }
    },
    purePictureClick(item) {
      // this.sucaiSelected = item
      if (this.backImgUrl === item) {
        this.$refs.imgEditor.addBackImg()
      } else {
        this.backImgUrl = item
      }
    },
    finishPictureClick(item) {
      if (this.finishSelectedList.includes(item)) {
        this.finishSelectedList = this.finishSelectedList.filter(key => key !== item)
      } else {
        this.finishSelectedList.push(item)
      }
    },
    pictureClick_zhuti(item) {
      this.zhutiSelect = item
      if (this.mainImgUrl === item) {
        this.$refs.imgEditor.addMainImage()
      } else {
        this.mainImgUrl = item
      }
    },
    downAll() {
      this.finishedPictureData.forEach((element, index) => {
        this.downloadFile(element, '成品图' + (index + 1))
      })
    },
    // 下载base64图片
    downloadFile(content, fileName) {
      var base64ToBlob = function(code) {
        const parts = code.split(';base64,')
        const contentType = parts[0].split(':')[1]
        const raw = window.atob(parts[1])
        const rawLength = raw.length
        const uInt8Array = new Uint8Array(rawLength)
        for (let i = 0; i < rawLength; ++i) {
          uInt8Array[i] = raw.charCodeAt(i)
        }
        return new Blob([uInt8Array], {
          type: contentType
        })
      }
      const aLink = document.createElement('a')
      const blob = base64ToBlob(content) // new Blob([content]);
      const evt = document.createEvent('HTMLEvents')
      evt.initEvent('click', true, true) // initEvent 不加后两个参数在FF下会报错  事件类型，是否冒泡，是否阻止浏览器的默认行为
      aLink.download = fileName
      aLink.href = URL.createObjectURL(blob)
      aLink.click()
    },
    // 文字操作相关
    addFont() {
      if (!this.fontValue) {
        this.$message.warning('请先输入文字')
        return
      }
      this.$refs.imgEditor.addFont(this.fontValue, this.fontForm)
    },
    boldFont() {
      this.fontForm.fontWeight = this.fontForm.fontWeight === 'bold' ? 'normal' : 'bold'
    },
    italicFont() {
      this.fontForm.fontStyle = this.fontForm.fontStyle === 'italic' ? 'normal' : 'italic'
    },
    activeFontChange(fontElement) {
      const { text, fontFamily, fontSize, fill, fontWeight, fontStyle, underline, textAlign, backgroundColor,
        // strokeWidth, stroke
        padding,
      } = fontElement

      this.fontValue = text
      this.fontForm = { fontFamily, fontSize, fill, fontWeight, fontStyle, underline, textAlign, backgroundColor,
        // strokeWidth, stroke
        padding,
      }
    },
    // 上传功能
    beforeAvatarUpload(file) {
      const fileName = starmerx.getRandomFileName(file.name)
      const newfile = new File([file], fileName)

      this.pageLoading = true
      starmerx.uploadImgAsync(newfile, newfile.name).then((res) => {
        this.pageLoading = false
        this.uploadFileList.unshift({ url: `${res.host}/${newfile.name}` })
      })
      return false
    },
    replaceBackImg(item) {
      if (this.backImgUrl === item.url) {
        this.$refs.imgEditor.addBackImg()
      } else {
        this.backImgUrl = item.url
      }
    },
    // 新增渐变
    addGradient() {
      this.$refs.imgEditor.addGradient()
    },
    // 重写渐变变量
    overrideGradient(obj) {
      this.gradientForm = obj
    },
  }
}
</script>

