<template>
  <div v-loading="loading" style="width: 100%;">
    <el-row :gutter="20" style="display: flex;align-items: center;padding: 5px 10px 0 10px;">
      <el-col :span="18">
        <slot />
      </el-col>
      <el-col :span="6">
        <!-- <el-button type="primary" size="small" @click="saveDraft">保存草稿</el-button>
        <el-button type="primary" size="small" @click="applyDraft">应用草稿</el-button> -->
        <el-button type="primary" size="small" @click="getResultImages">保存</el-button>
        <el-button size="small" @click="canvasClear">清空</el-button>
      </el-col>
    </el-row>
    <el-row :gutter="20" style="display: flex;align-items: center;padding: 0 10px 5px 10px;height: 43px;">
        <el-col :span="5" v-if="activeEle">
          当前图层：{{activeEle.starmerxElementType}}
        </el-col>
        <el-col :span="5" v-show="activeEle">
          <div style="display: flex;align-items: center;">
            透明度：<el-slider v-model="eleTransparency" @change="transparencyChange" style="flex: 1;"/>
          </div>
        </el-col>
        <el-col :span="5" v-show="activeEle">
          <div style="display: flex;align-items: center;">
            模糊：<el-slider v-model="filterObj.blur" @change="blurChange" style="flex: 1;"/>
          </div>
        </el-col>
        <el-col :span="4" v-show="activeEle">
          <div style="display: flex;align-items: center;">
            阴影：
            <el-color-picker v-model="shadowObj.color" show-alpha @change="shadowChange" />
            <el-popover
              placement="top"
              width="200"
              trigger="click">
              阴影水平偏移量
              <el-slider
                v-model="shadowObj.offsetX"
                :min="-2000"
                :max="2000"
                @change="shadowChange"/>
              阴影垂直偏移量
              <el-slider
                v-model="shadowObj.offsetY"
                :min="-2000"
                :max="2000"
                @change="shadowChange"/>
              阴影模糊
              <el-slider
                v-model="shadowObj.blur"
                :min="-2000"
                :max="2000"
                @change="shadowChange"/>
              <i slot="reference" class="el-icon-s-help" style="font-size: 25px" />
            </el-popover>
          </div>
        </el-col>
    </el-row>
    <div class="canvas-box">
      <canvas id="fabricCanvas" class="fabric-canvas" />
    </div>
  </div>
</template>

<script>
import _ from 'lodash'
import { fabric } from 'fabric'
export default {
  props: {
    backImgUrl: {
      type: String,
      default: ''
    },
    backColor: {
      type: String,
      default: ''
    },
    mainImgUrl: {
      type: String,
      default: ''
    },
    frontImgUrl: {
      type: String,
      default: ''
    },
    fontForm: {
      type: Object,
      default: {}
    },
    gradientForm: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      boxWidth: 800,
      boxHeight: 800,
      fabricCanvas: undefined,
      activeEle: undefined,
      boxScale: 1,
      loading: false,
      eleTransparency: 100,
      filtersAttrList: ['blur'],
      filterObj: {
        blur: 0,
      },
      // 阴影相关
      shadowObj: {
        // color: '',
        // offsetX: 0,
        // offsetY: 0,
        // blur: 0
      },
      canvasStrJson: undefined
    }
  },
  watch: {
    backImgUrl: {
      immediate: true,
      handler(val) {
        this.addBackImg()
      }
    },
    mainImgUrl: {
      immediate: true,
      handler(val) {
        this.addMainImage()
      }
    },
    backColor: {
      immediate: true,
      handler(val) {
        this.addBackColor()
      }
    },
    // 前景图
    frontImgUrl: {
      immediate: true,
      handler(val) {
        this.addFrontImg()
      }
    },
    // 文本样式变化
    fontForm: {
      immediate: true,
      deep: true, // true为进行深度监听，false为不进行深度监听
      handler(val) {
        this.editFontStyle()
      }
    },
    // 渐变参数变化
    gradientForm: {
      immediate: true,
      deep: true, // true为进行深度监听，false为不进行深度监听
      handler(val) {
        this.gradientChange()
      }
    },
  },
  mounted() {
    this.init()
  },
  methods: {
    init() {
      fabric.Object.prototype.set({     
        cornerStyle: 'circle',
        cornerColor: '#3880ff',
        cornerSize: 12,
        borderColor: 'rgba(0, 94, 224, 1)',
        transparentCorners: false,
      })
      // fabric Cavnas 预设选项
      const CANVAS_OPTIONS = {
        width: this.boxWidth,
        height: this.boxHeight,
        preserveObjectStacking: true, // 元素对象被选中时保持在当前z轴，不会跳到最顶层
        fireRightClick: false,
        stopContextMenu: false,
        hoverCursor: 'default',
        defaultCursor: 'default',
        selection: true
      }
      this.fabricCanvas = new fabric.Canvas('fabricCanvas', CANVAS_OPTIONS)
      this.boxScale = this.boxWidth / this.boxHeight

      // 监听窗口宽高变化
      this.resizeBox()
      window.addEventListener('resize', _.throttle(() => {
          this.resizeBox()
      }, 300))
      // 监听键盘输入
      document.addEventListener('keydown', this.showkey)
      // 监听鼠标按下事件
      this.fabricCanvas.on({
        'mouse:down': e => {
          this.hideGradientGuides(e)
          this.getSActiveEle()
        }
      })
      document.addEventListener('mousedown', e => {
        const classList = e && e.target && e.target.classList
        if (!(classList && classList.contains('fabric-canvas'))) {
          this.hideGradientGuides(e)
        }
      })

      this.addBackImg()
      this.addMainImage()
      this.addBackColor()
      this.addFrontImg()
    },
    // 新增渐变
    addGradient() {
      if (!this.fabricCanvas) return
      // this.deleteAssignElement('背景图')
      this.deleteAssignElement('渐变参考线')

      const line = new fabric.Line([0, this.boxHeight / 2, this.boxWidth, this.boxHeight / 2], {
        stroke: 'rgba(0, 128, 0, 1)',
        strokeWidth: 10,
        starmerxElementType: '渐变参考线',
        visible: true
      });
      line.setControlsVisibility({
        bl: false, br: false, mb: false, ml: true, mr: true, mt: false, tl: false, tr: false, mtr: true
      })
      line.bringToFront()
      line.on({
        'modified': e => {
          this.gradientChange()
        },
        'removed': e => {
          this.deleteAssignElement('渐变背景')
        },
        'selected': e => {
          line.bringToFront()
        }
      })
      this.fabricCanvas.add(line)

      const rect = new fabric.Rect({
        width: 800, height: 800, left: 0, top: 0,
        starmerxElementType: '渐变背景',
        // selectable: false,
      })
      // 线性渐变Coords
      const linearCoords = { x1: 0, y1: this.boxHeight / 2, x2: this.boxWidth, y2: this.boxHeight / 2 }
      // 径向渐变Coords
      const radialCoords = {
        x1: (linearCoords.x1 + linearCoords.x2) / 2,
        y1: (linearCoords.y1 + linearCoords.y2) / 2,
        x2: (linearCoords.x1 + linearCoords.x2) / 2,
        y2: (linearCoords.y1 + linearCoords.y2) / 2,
        r1: 0,
        r2: Math.sqrt(Math.pow(linearCoords.x1 - linearCoords.x2, 2) + Math.pow(linearCoords.y1 - linearCoords.y2, 2))
      }
      const gradient = new fabric.Gradient({
        type: this.gradientForm.gType,
        gradientUnits: 'pixels',
        coords: this.gradientForm.gType === 'linear' ? linearCoords : radialCoords,
        colorStops: _.uniqBy(this.gradientForm.colorStops, 'offset').filter(ele => !!ele.color)
      })
      rect.set('fill', gradient);
      this.fabricCanvas.add(rect)
      this.fabricCanvas.sendToBack(rect)
    },
    gradientChange() {
      if (!this.fabricCanvas) return
      const line = this.fabricCanvas.getObjects().find(ele => ele.starmerxElementType === '渐变参考线')
      if (!line) return
      const pointLT = line.getPointByOrigin('left', 'top')
      const pointRB = line.getPointByOrigin('right', 'bottom')
      this.fabricCanvas.forEachObject(obj => {
        if (obj.starmerxElementType === '渐变背景') {
          if (obj.fill) {
            const fillObj = obj.fill.toObject()
            fillObj.type = this.gradientForm.gType

            // 线性渐变Coords
            const linearCoords = { x1: pointLT.x, y1: pointLT.y, x2: pointRB.x, y2: pointRB.y }
            // 径向渐变Coords
            const radialCoords = {
              x1: (linearCoords.x1 + linearCoords.x2) / 2,
              y1: (linearCoords.y1 + linearCoords.y2) / 2,
              x2: (linearCoords.x1 + linearCoords.x2) / 2,
              y2: (linearCoords.y1 + linearCoords.y2) / 2,
              r1: 0,
              r2: Math.sqrt(Math.pow(linearCoords.x1 - linearCoords.x2, 2) + Math.pow(linearCoords.y1 - linearCoords.y2, 2))
            }
            fillObj.coords = this.gradientForm.gType === 'linear' ? linearCoords : radialCoords,
            fillObj.colorStops = _.uniqBy(this.gradientForm.colorStops, 'offset').filter(ele => !!ele.color)
            const newGradient = new fabric.Gradient(fillObj)
            obj.set('fill', newGradient);

            this.fabricCanvas.renderAll()
          }
        }
      })
    },
    // 控制渐变参考线显示隐藏
    hideGradientGuides(e) {
      const line = this.fabricCanvas.getObjects().find(ele => ele.starmerxElementType === '渐变参考线')
      if (!line) return
      const downType = e && e.target && e.target.starmerxElementType
      if (['渐变背景', '渐变参考线'].includes(downType)) {
        line.set({visible: true})
        line.bringToFront()
      } else {
        line.set({visible: false})
        if (this.activeEle && this.activeEle.starmerxElementType === '渐变参考线') {
          this.fabricCanvas.discardActiveObject()
          this.getSActiveEle()
        }
      }
      this.fabricCanvas.renderAll()
    },
    resizeBox() {
      const pageWidth = document.body.clientWidth
      const pageHeight = document.body.clientHeight
      if (this.fabricCanvas) {
        const widthZoom = Math.max(pageWidth - 910, 100) / this.boxWidth
        const heightZoom = Math.max(pageHeight - 380, 100) / this.boxHeight
        const myZoom  = Math.min(widthZoom, heightZoom)
        this.fabricCanvas.setWidth(this.boxWidth * myZoom )
        this.fabricCanvas.setHeight(this.boxHeight * myZoom)
        this.fabricCanvas.setZoom(myZoom)
      }
    },
    // 得到按键的信息
    showkey(e) {
      const key = e.keyCode;
      switch (key) {
        case 46: // "按了delete键！"
          this.getSActiveEle()
          if (this.activeEle) {
            if (this.activeEle.starmerxImageUrl) {
              this.$emit('deleteImg', this.activeEle.starmerxElementType, this.activeEle.starmerxImageUrl)
            }
            this.fabricCanvas.remove(this.activeEle);
            this.getSActiveEle()
          }
        break;
      }
    },
    // 获取当前活动元素
    getSActiveEle() {
      this.activeEle = this.fabricCanvas && this.fabricCanvas.getActiveObject()
      if (this.activeEle) {
        // 透明度
        this.eleTransparency = this.activeEle.getObjectOpacity() * 100
        // 模糊度
        const blurObj = this.activeEle.filters && this.activeEle.filters[this.filtersAttrList.findIndex(ele => ele === 'blur')]
        this.filterObj.blur = blurObj ? (blurObj.blur * 100) : 0
        // 阴影
        this.shadowObj = this.activeEle.shadow ? this.activeEle.shadow.toObject() : {}
        if (this.activeEle.starmerxElementType === '文本') {
          this.$emit('activeFontChange', this.activeEle)
        }
      }
    },
    // 活动元素透明度变化
    transparencyChange() {
      this.activeEle = this.fabricCanvas.getActiveObject()
      if (this.activeEle) {
        this.activeEle.setOptions({opacity: this.eleTransparency / 100})
        this.fabricCanvas.renderAll()
      }
    },
    // 活动元素模糊度变化
    blurChange() {
      this.activeEle = this.fabricCanvas.getActiveObject()
      if (!this.activeEle) return
      if (['渐变背景', '渐变参考线', '文本'].includes(this.activeEle.starmerxElementType)) {
        this.$message.warning('当前图层不能模糊')
        return
      }

      const index = this.filtersAttrList.findIndex(ele => ele === 'blur')
      if (this.activeEle.filters && this.activeEle.filters[index]) {
        this.applyFilterValue(index, 'blur', (this.filterObj.blur / 100))
      } else {
        const blurObj = new fabric.Image.filters.Blur({blur: (this.filterObj.blur / 100)})
        this.applyFilter(index, blurObj)
      }
    },
    // 活动元素阴影变化
    shadowChange() {
      this.activeEle = this.fabricCanvas.getActiveObject()
      if (this.activeEle) {
        this.activeEle.set({  shadow: this.shadowObj })
        this.fabricCanvas.renderAll()
      }
    },
    applyFilterValue(index, prop, value) {
      const canvas = this.fabricCanvas
      const obj = canvas.getActiveObject();
      obj.filters[index][prop] = value;
      obj.applyFilters();
      canvas.renderAll();
    },
    applyFilter(index, filter) {
      const canvas = this.fabricCanvas
      const obj = canvas.getActiveObject();
      obj.filters[index] = filter;
      obj.applyFilters();
      canvas.renderAll();
    },
    // 添加主体图
    addMainImage() {
      if (!this.mainImgUrl || !this.fabricCanvas) return
      // 已存在当前主体图
      const existImgEle = this.fabricCanvas._objects.find(ele => (ele.starmerxImageUrl === this.mainImgUrl) && (ele.starmerxElementType === '主体图'))
      if (existImgEle) {
        existImgEle.bringToFront()
        this.fabricCanvas.setActiveObject(existImgEle)
        return
      }

      const _this = this
      this.addImage(this.mainImgUrl, (oImg) => {
        const objScale = oImg.width / oImg.height
        let convert = 1
        if (objScale >= _this.boxScale) {
          convert = _this.fabricCanvas.getHeight() / oImg.height
        } else {
          convert = _this.fabricCanvas.getWidth() / oImg.width
        }
        oImg.scale(convert / this.fabricCanvas.getZoom() / 2)
        oImg.left = 90
        oImg.top = 90
        oImg.set({ starmerxElementType: '主体图' })

        // // 删除之前的主体图
        // const mainImageElement = this.fabricCanvas._objects.find(item => item.starmerxElementType === '主体图')
        // if (mainImageElement) {
        //   this.fabricCanvas.remove(mainImageElement)
        // }
        _this.fabricCanvas.add(oImg)
        // 移至顶层
        oImg.bringToFront()
        _this.fabricCanvas.setActiveObject(oImg)
      })
    },
    // 添加前景图
    addFrontImg() {
      if (!this.frontImgUrl || !this.fabricCanvas) return
      // 已存在当前前景图
      const existImgEle = this.fabricCanvas._objects.find(ele => (ele.starmerxImageUrl === this.frontImgUrl) && (ele.starmerxElementType === '前景图'))
      if (existImgEle) {
        existImgEle.bringToFront()
        this.fabricCanvas.setActiveObject(existImgEle)
        return
      }

      const _this = this
      this.addImage(this.frontImgUrl, (oImg) => {
        const objScale = oImg.width / oImg.height
        let convert = 1
        if (objScale >= _this.boxScale) {
          convert = _this.fabricCanvas.getWidth() / oImg.width
        } else {
          convert = _this.fabricCanvas.getHeight() / oImg.height
        }
        oImg.scale(convert / this.fabricCanvas.getZoom() / 2)
        oImg.set({ starmerxElementType: '前景图' })

        _this.fabricCanvas.add(oImg)
        // 移至顶层
        oImg.bringToFront()
        _this.fabricCanvas.setActiveObject(oImg)
      })
    },
    // 添加背景图
    addBackImg() {
      if (!this.backImgUrl || !this.fabricCanvas) return
      // 已存在当前背景图
      const existImgEle = this.fabricCanvas._objects.find(ele => (ele.starmerxImageUrl === this.backImgUrl) && (ele.starmerxElementType === '背景图'))
      if (existImgEle) {
        this.fabricCanvas.setActiveObject(existImgEle)
        return
      }

      const _this = this
      this.addImage(this.backImgUrl, (oImg) => {
        const objScale = oImg.width / oImg.height
        let convert = 1
        if (objScale >= _this.boxScale) {
          convert = _this.fabricCanvas.getHeight() / oImg.height
        } else {
          convert = _this.fabricCanvas.getWidth() / oImg.width
        }
        oImg.scale(convert / this.fabricCanvas.getZoom())
        oImg.set({ starmerxElementType: '背景图' })
        // 删除之前的背景图
        const backImgElement = this.fabricCanvas._objects.find(item => item.starmerxElementType === '背景图')
        if (backImgElement) {
          this.fabricCanvas.remove(backImgElement)
        }
        _this.fabricCanvas.add(oImg)
        _this.fabricCanvas.sendToBack(oImg)
      })
    },
    // 添加背景色
    addBackColor() {
      if (!this.backColor || !this.fabricCanvas) return
      const canvas = this.fabricCanvas
      canvas.setBackgroundColor(this.backColor, canvas.renderAll.bind(canvas))
    },
    // 生成图片base64
    getResultImages() {
      this.fabricCanvas.renderAll()
      const url = this.fabricCanvas.toDataURL({
        format: 'png',
        multiplier: 1600 / this.fabricCanvas.getWidth()
      })
      this.$emit('resultSrc', url)
    },
    // 手动选中元素
    setActiveElement(type) {
      const activeElement = this.fabricCanvas._objects.find(item => item.starmerxElementType === type)
      if (activeElement) {
        activeElement.bringToFront()
        this.fabricCanvas.setActiveObject(activeElement)
      }
    },
    // 删除指定元素
    deleteAssignElement(type) {
      const assignElement = this.fabricCanvas._objects.find(item => item.starmerxElementType === type)
      if (assignElement) {
        this.fabricCanvas.remove(assignElement)
      }
    },
    /**
     * 根据url加载fabricImage 可通过回调设置属性
     */
    addImage(url, callback) {
      const img = new Image()
      img.crossOrigin = 'Anonymous'
      // img.setAttribute('crossOrigin', 'anonymous');
      this.loading = true
      img.onload = () => {
        this.loading = false
        const fImage = new fabric.Image(img)
        fImage.starmerxImageUrl = url
        // fImage.crossOrigin = 'Anonymous'
        callback(fImage)
      }
      img.src = url
    },
    // 清空
    canvasClear() {
      this.$emit('canvasEmptying')
      this.fabricCanvas.clear()
      this.getSActiveEle()
    },
    urlDataToBlob(urlData) {
      const arr = urlData.split(',')
      const mime = arr[0].match(/:(.*?);/)[1]
      const bstr = atob(arr[1])
      let n = bstr.length
      const u8arr = new Uint8Array(n)
      // eslint-disable-next-line no-plusplus
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n)
      }
      return new Blob([u8arr], { type: mime })
    },
    uploadFile(url, blob) {
      const formData = new FormData()
      const request = new XMLHttpRequest()
      formData.append('image', blob)
      request.open('POST', url, true)
      request.send(formData)
    },
    // 添加文字
    addFont(text, fontForm) {
      const text1 = new fabric.IText(text, { left: 100, top: 100, ...fontForm })
      text1.set({ starmerxElementType: '文本' })
      
      this.fabricCanvas.add(text1)
      // 移至顶层
      text1.bringToFront()
      this.fabricCanvas.setActiveObject(text1)
      this.getSActiveEle()
    },
    // 编辑文本样式
    editFontStyle() {
      if (this.activeEle && this.activeEle.starmerxElementType === '文本') {
        this.activeEle.set(this.fontForm)
        this.fabricCanvas.renderAll()
      }
    },
    // 保存草稿
    saveDraft() {
      const canvasJson = this.fabricCanvas.toJSON(['starmerxElementType', 'starmerxImageUrl'])
      this.canvasStrJson = JSON.stringify(canvasJson)
    },
    // 应用草稿
    applyDraft() {
      if (!this.canvasStrJson) return
      this.canvasClear()
      this.fabricCanvas.loadFromJSON(JSON.parse(this.canvasStrJson), () => {
        const line = this.fabricCanvas.getObjects().find(ele => ele.starmerxElementType === '渐变参考线')
        if (line) {
          line.setControlsVisibility({
            bl: false, br: false, mb: false, ml: true, mr: true, mt: false, tl: false, tr: false, mtr: true
          })
          line.on({
            'modified': e => {
              this.gradientChange()
            },
            'removed': e => {
              this.deleteAssignElement('渐变背景')
            },
            'selected': e => {
              line.bringToFront()
            }
          })
          // 重写渐变变量
          this.overrideGradient()
        }
      })
    },
    // 重写渐变变量
    overrideGradient() {
      const gradientRect = this.fabricCanvas.getObjects().find(ele => ele.starmerxElementType === '渐变背景')
      if (gradientRect) {
        const { type, colorStops } = gradientRect.fill.toObject()
        this.$emit('overrideGradient', { gType: type, colorStops })
      }
    }
  }
}
</script>

<style scoped>
.canvas-box {
  width: 100%;
  display: flex;
  justify-content: center;
}

#fabricCanvas {
  border: 1px dashed black;
}
</style>
