<template>
  <div v-if="src" class="pop-img-warp pop-img">
    <el-image :src="src" :style="{width: width, height: width}" class="thumb" :alt="alt" lazy @mouseenter="startImgPreview" @mouseleave="stopImgPreview" @mousemove="watchMouseLocation" @click="pictureClick">
      <div slot="placeholder" class="image-slot pictureLoading">
        <span class="dot pictureLoadingSpan" :style="`line-height:${width};`">加载中...</span>
      </div>
    </el-image>
    <img v-if="isPreview&&position==='left'" :style="{left:previewX+20+'px',top:previewY+20+'px'}" class="lg-img-preview" :src="previewsrc" :alt="alt">
    <img v-if="isPreviewBottom&&position==='left'" :style="{left:previewX+20+'px',bottom:100+'px'}" class="lg-img-preview" :src="previewsrc" :alt="alt">
    <img v-if="isPreview&&position==='right'" :style="{left:previewX-220+'px',top:previewY+20+'px'}" class="lg-img-preview" :src="previewsrc" :alt="alt">
    <img v-if="isPreviewBottom&&position==='right'" :style="{left:previewX-220+'px',bottom:100+'px'}" class="lg-img-preview" :src="previewsrc" :alt="alt">
    <!-- 显示图片尺寸 -->
    <div
      v-if="showSize && (isPreview || isPreviewBottom) && imageSize"
      :style="{
        top: isPreview ? (previewY + 20 + 'px') : undefined,
        bottom: isPreviewBottom ? (100 +'px' ) : undefined,
        left: previewX + 20 + (imageSize.width > 400 ? 400 : imageSize.width) - 110 + 'px',
      }"
      class="lg-size-preview"
    >
      <span>{{ imageSize.width }} * {{ imageSize.height }}</span>
    </div>
    <el-dialog v-if="isShow" title="大图展示" :visible.sync="isShow" :append-to-body="isAppend">
      <div class="lg-image-warp">
        <img class="lg-image" :src="src" :alt="alt">
      </div>
    </el-dialog>
  </div>
</template>

<script>
// import utils from '@/utils/starmerx'
export default {
  name: 'PopImg',
  props: {
    width: {
      type: String,
      default: ''
    },
    src: {
      type: String,
      default: ''
    },
    bigsrc: {
      type: String,
      default: ''
    },
    position: {
      type: String,
      default: 'left'
    },
    alt: {
      type: String,
      default: ''
    },
    isAppend: {
      type: Boolean,
      default: false
    },
    showSize: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isShow: false,
      isPreview: false,
      isPreviewBottom: false,
      previewX: 0,
      previewY: 0,
      imageSize: undefined
    }
  },
  computed: {
    previewsrc() {
      return this.bigsrc ? this.bigsrc : this.src
    }
  },
  created() {
    if (this.showSize) {
      // 获取图片尺寸
      utils.getImageSize(this.src).then(sizeInfo => {
        const { width, height } = sizeInfo
        if (width && height) {
          this.imageSize = { width, height }
        }
      })
    }
  },
  methods: {
    startImgPreview() {
      this.isPreview = true
    },
    stopImgPreview() {
      this.isPreview = false
      this.isPreviewBottom = false
    },
    watchMouseLocation(e) {
      if (e.clientY > 600) {
        this.isPreview = false
        this.isPreviewBottom = true
      }
      this.previewX = e.clientX
      this.previewY = e.clientY
    },
    pictureClick() {
      this.$emit('pictureClick')
    }
  }

}
</script>

<style scoped lang="less">
  .pop-img-warp{
    .thumb{
      margin: 0 auto;
      background-size: cover !important
    }
  }
  .lg-image-warp{
    text-align: center;
    .lg-image{
      max-width: 100%;
    }
  }
  .lg-img-preview{
    z-index: 99999;
    position: fixed;
    max-width: 200px;
  }
  .lg-size-preview{
    z-index: 99999;
    position: fixed;
    width: 110px;
    padding: 5px;
    background-color: #606266;
    opacity: 0.75;
    >span {
      display: inline-block;
      width: 100%;
      color: #fff;
      line-height: 20px;
      font-size: 16px;
      text-align: center;
    }
  }
  .pictureLoadingSpan{
display: flex;
justify-content: center;
align-items: center;
font-size: 14px;
color: #c0c4cc;
  }
  .pictureLoading{
background:#fafafa;width:100%;height:100%
  }
</style>
